/* ====== main block ===== */

.main {
  display: flex;
  justify-content: space-between;
  width: 1126px;
  height: 843px;
  padding-top: 40px;
  margin: 0 auto;

  &__heading-2 {
    display: block;
    height: 59px;
    padding: 0 0 35px;
    font-size: 2.4rem;
    font-weight: bold;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 778px;
  }

  &__btn-container {
    display: flex;
    justify-content: center;
    padding: 30px 0 103px;
  }
}
